*{
    margin: 0;
    padding: 0;
}

ul li{
    list-style: none;
}

.navBar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* padding: 0 3rem; */
    padding: 0 20%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    align-items: center;
    background-color: white;
}

ul a{
    text-decoration: none;
    color: black;
}

.logo{
    font-family: 'Bungee Shade', cursive;
    font-size: 1.75rem;
}
.logo li span{
    color: #F65420;
}

.nav-btn{
    display: flex;
    flex-direction: row;
}

.nav-btn li{
    margin: 0 15px;
}

.login-btn{
    font-family: 'Itim', cursive;
    background: none;
    outline: none;
    border: none;
    padding: 5px 0 ;
    border-radius: 7px;
    cursor: pointer;
    font-size: 1.2rem;
    color: #586375;
    transition: .4s;
}
.login-btn:hover{
    color: #DC0560;
    transform: translateY(5px);
}

.reg-btn{
    font-family: 'Itim', cursive;
    background: none;
    outline: none;
    border: none;
    padding:  5px 15px;
    border-radius: 7px;
    cursor: pointer;
    font-size: 1.2rem;
    color: #F65420;
    transition: .4s;
}
.reg-btn:hover{
    background-color: #F65420;
    color: white;
}

@media only screen and (max-width:480px){
    .navBar{
        padding: 0 1em;
    }
    .nav-btn li{
        margin: 0 5px;
    }
}
